/* ESTILOS PAGINA PRINCIPAL */

.carrusel {
    margin: auto;
    padding: 20px;
}

.cardprin {
    margin-top: 50px;
}

.cardp:hover {
    border: 2px solid #333;
    color: white;
    background: #333;
}

/* ESTILOS INICIO SESIÃ“N */

.Login {
    margin: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    width: 60%;
    padding: 20PX;
    border: 1px solid rgb(192, 186, 186);
    border-radius: 5PX;
}

.Filaslogin {
    margin: 0;
    padding: 0;
}

/* ESTILOS CREAR CUENTA */

.Cuenta {
    margin: auto;
    margin-top: 10px;
    margin-bottom: 20px;
    padding: 20PX;
    border: 1px solid rgb(143, 139, 139);
    border-radius: 5PX;
    align-items: center;
}

/* ESTILOS JUEGOS */

.contjuegos {
    margin: 20px;
}

.caraJuego {
    margin-top: 25px;
}

.caraJuego:hover {
    box-shadow: 3px 3px 3px 3px rgb(92, 91, 91);
}

/* ESTILOS SOPORTE TECNICO */

.jumbo {
    background: url(../images/soporte.jpg);
    background-attachment: fixed;
    background-size: cover;
}

.media {
    border-bottom: 2px solid rgb(0, 119, 255);
    padding-top: 10px;
}

.media:hover {
    border-bottom: 2px solid rgb(117, 117, 117);
    box-shadow: 3px 3px 3px 3px rgb(92, 91, 91);
}

/* ESTILOS CONTACTENOS */

.contcontactenos {
    margin-top: 50px;
}

.parrafo {
    border-bottom: solid 1px rgb(172, 172, 172);
}



